.App {
  text-align: center;
}

body {
  background-color: #C0EEE4;
  font-family: Comic Sans MS, Comic Sans, cursive;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.Wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Ingredient {
  width: 150px;
  height: 200px;
  border-radius: 8px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  margin: 20px;
  transition: 0.3s;
}

.Ingredient:hover{
  width: 160px;
  height: 210px;
}

.Ingredient img{
  height: 80px;
  width: 100px;
  border-radius: 10px;
  margin: 10px;
}

.Ingredient p{
  margin: 0;
  font-size: 12px;
}

.Ingredient span{
  font-size: 14px;
}

.Steps {
  overflow-y: scroll;
  position: relative;
  top: 300px;
  visibility: hidden;
  transition: 0.3s;
  opacity: 0;
}

.Ingredient:hover .Steps {
  visibility: initial;
  top: 0px;
  opacity: 1;
}

.InputWrapper input {
  width: 300px;
  height: 30px;
  border-radius: 10px;
  padding: 10px;
  margin: 20px;
  border: 0px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.5);
}

.InputWrapper button{
  height: 40px;
  width: 80px;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  background-color: #5BC0F8;
}